import React, {useState} from 'react';
import data from './data';
import './style.css'

const Question = (props) => {
    const [showAnswer, setShowAnswer] = useState(false);
    const {question, answer} = props.data;
    return (
        <div className="question">
            <div className="quest-sec">
                <h3>{question}</h3>
                <i className={!showAnswer ? "fa fa-plus": "fa fa-minus"} onClick={() => setShowAnswer(!showAnswer)}></i>
            </div>
            <p>{showAnswer && answer}</p>
            
        </div>
    );
    

}
const Functionality = () => {
    return <>
        <div className="main">
            <h2>Frequently Asked Questions</h2>
            {
                data.map((quest, index) => {
                    return(
                        <>
                            <Question key={index} data={{...quest, index}}/>
                        </>
                    )
                })
            }
        </div>
    </>
}
export default Functionality;